.dialog{
    .mask{
        width: 100%;
        height:100%;
        position:fixed;
        background: #00000066;
        z-index:999;
        top: 0;
        left: 0;
    }
    .content{
        position:absolute;
        width:630px;
        min-height: 100px;
        border-radius:8px;
        top:50%;
        left:50%;
        transform: translateX(-50%) translateY(-50%);
        background: #fff;
        z-index:9999;
        padding:20px 0;
        .ant-form-horizontal{
            display: flex;
            flex-direction: column;
        }
        form-item{
            display: flex;
            width: 100%;
            align-items: center;
            margin:15px 0px;
            padding:0 30px;
            text-align: center;
            padding-left:13%;
            span{
                padding-right:20px;
            }
        }
        .department,.position{
            padding-left:27px;
        }
        input{
            width:75%;
        }
        .button{
            text-align: center;
            margin-bottom:10px;
            button{
                margin:10px 10px;
            }
        }
    }
}

